*{box-sizing:border-box}html{height:100%}body{margin:0;background-color:#022a58;font-family:'Anton', sans-serif;color:white;display:grid;grid-template-rows:70px 1fr 70px;justify-content:stretch;height:100%}.body-light{background-color:white}header{background-color:rgba(5,0,22,0.3);border-bottom-left-radius:30px;border-bottom-right-radius:30px;grid-row:1/2;padding-top:10px}.background-blue{background-color:#022a58;color:white}main{overflow:scroll;grid-row:2/3}footer{background-color:rgba(5,1,20,0.3);border-top-left-radius:30px;border-top-right-radius:30px;grid-row:3/4;align-self:stretch;padding-top:15px}.header__headline{align-self:center;margin:auto;text-align:center}.header__headline--gradient{background-image:linear-gradient(45deg, #ff865a 0%, #ff9a72 100%);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.nav{display:flex;justify-content:space-around}.nav__link-1{grid-column:1/2}.nav__link-2{grid-column:2/3}.nav__link-3{grid-column:3/4}.nav__link-4{grid-column:4/5}.mb-10{margin-bottom:10px}.mb-30{margin-bottom:30px}.mt-10{margin-top:10px}.d-none{display:none}.ff-roboto{font-family:'Roboto', sans-serif}.button-answer,.button-answer1,.button-answer2,.button-answer3,.button-answer4,.button-answer5,.button-answer6{display:block;font-family:'Anton', sans-serif;font-size:inherit;text-decoration:none;background-color:#022a58;border:none;color:white;padding:10px 25px 10px 25px;margin:auto;border-radius:7px}.hidden{display:none}.svg-fill{fill:#ff865a}.question-box{position:relative;background:#ff865a;background:linear-gradient(45deg, #ff865a 0%, #ff9a72 100%);padding:40px;margin:60px 20px 0px 20px;border-radius:30px;box-shadow:4px 4px 4px 1px rgba(0,0,0,0.2);scroll-snap-align:center}.question-box__bookmark--marked1,.question-box__bookmark--marked2,.question-box__bookmark--marked3,.question-box__bookmark--marked4,.question-box__bookmark--marked5,.question-box__bookmark--marked6{fill:#ff0;height:60px;position:absolute;right:25px;top:-14px}.question-box__bookmark--unmarked1,.question-box__bookmark--unmarked2,.question-box__bookmark--unmarked3,.question-box__bookmark--unmarked4,.question-box__bookmark--unmarked5,.question-box__bookmark--unmarked6{fill:#EDEDED;height:60px;position:absolute;right:25px;top:-14px}.question-box__text{margin-top:5px;margin-bottom:25px}.question-box:last-of-type{margin-bottom:50px}.question-box__answer{display:none}.question-box__tags{display:flex;flex-wrap:wrap;padding:0;list-style:none}.question-box__tags li{font-size:0.8em;background-color:rgba(254,254,254,0.2);margin:7px 7px;padding:10px}.create-box{background:#ff865a;background:linear-gradient(45deg, #ff865a 0%, #ff9a72 100%);padding:30px 40px 30px 40px;margin:40px 20px 0px 20px;border-radius:30px;box-shadow:4px 4px 4px 1px rgba(0,0,0,0.2)}.create-box__headline{margin:0}.create-box__textarea{font-family:'Roboto', sans-serif;color:#022a58;border:none;outline:none;background-color:rgba(254,254,254,0.5);padding:8px;resize:none}.profile-light{color:#022a58}.profile__profile-picture{display:block;height:150px;width:150px;border-radius:50%;border:10px;border:5px solid #ff865a;margin:40px auto 0 auto;box-shadow:4px 4px 4px 1px rgba(0,0,0,0.2)}.profile__headline{display:block;text-align:center}.profile__about-me{margin:40px}.profile__about-me--headline{margin:0}.profile__about-me--text{line-height:1.5;margin:5px 0 40px 0}.profile__skills{display:flex;flex-wrap:wrap;padding:0;list-style:none}.profile__skills li{font-size:0.8em;background-color:rgba(254,254,254,0.2);margin:0 10px 20px 0;padding:10px}.profile-features{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;margin:auto}.profile__logout{font-family:'Anton', sans-serif;font-size:inherit;text-decoration:none;background:#ff865a;background-color:linear-gradient(45deg, #ff865a 0%, #ff9a72 100%);border:none;color:white;padding:10px 25px 10px 25px;border-radius:7px;margin-left:50px}
